@import "./core.css";

@import "./Button.css";

@import "./NetworkIcon.css";

._mottled_SliderButtons {
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  border-radius: 0.75rem;
  border-width: 0px;
  padding: 0.25rem;
}

._mottled_button_ow {
  background-color: transparent;
  border: none;
  color: #ffffff;
}

._mottled_button_ow:hover {
  background-color: transparent;
}

._mottled_handle {
  z-index: 100;
  position: absolute;
  height: 90%;
  color: #ffffff;
  border: none !important;
}

.handle_right {
  right: 0.25rem;
}

.font-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
  text-transform: capitalize;
}

.positioning {
  position: relative;
}
